
/*********************************************

    #CSS INDEX
     I - Text Manipulation
    II - Height and Width Manipulation
   III - Background Manipulation
    IV - Button Component CSS
     V - Image Manipulation
    VI - Icon Manipulation

********************************************/

    body{
        font-family: 'Inter', sans-serif !important;
        margin:0;
        background-color:#23252C;
    }

/*********************************************

    I - Text Manipulation

**********************************************/
    .fw-500{
        font-weight: 500 !important;
        
    }
    .fw-700{
        font-weight: 700 !important;
    }

    .line-h-16{
        line-height: 16px;
    }
    .text-small{
        font-size:11px;
    }

    .text-white{
        color:white !important;
    }

    .text-darker{
        color:#374151 !important;
    }

    .text-prestige{
        color:#F3BF99 !important;
    }

    .text-gray-light{
        color: #dcdcdc !important;
    }

/*********************************************

    II - Height and Width Manipulation

**********************************************/
    .h-full{
        height:100vh;
    }

    .h-1800{
        height:1800px;
    }

    .w-25{
        width:25px;
    }

    .w-20{
        width:20px;
    }

    .w-12{
        width:12.8px;
    }

/*********************************************

    III - Background Manipulation

**********************************************/
    .bg-darker{
        background-color: #1A1B21 !important;
    }

    .bg-darker-dark{
        background-color:#161619;
    }
/*********************************************

    IV - Button Component

**********************************************/

/*********************************************

    V - Image Manipulation

**********************************************/
.img-box{
    height:280px;
}

.img {
    background-image: url('../images/FotoRecortada2.png ');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    height:100%;
}


/**********************************************

    VI - Icon Manipulation

***********************************************/

.social-icon-25 {
    width:25px;
    height:25px;
}

.social-icon-30 {
    width:30px;
    height:30px;
}